<template>
    <v-container>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                settingsAccount
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{changeNameAndLastNameText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card class="pa-5" style="margin-top: 60px;">
            <v-form v-model="validForm">
                <v-text-field
                        v-model="name"
                        :rules="[rules.required]"
                        :label="nameText"
                        required
                ></v-text-field>

                <v-text-field
                        v-model="lastName"
                        :rules="[rules.required]"
                        :label="lastNameText"
                        required
                ></v-text-field>
            </v-form>
            <v-btn block color="primary" style="margin-top: 10px" @click="saveNameAndLastName"
                   :disabled="buttonIsDisabled">
                {{saveText}}
            </v-btn>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex';

    export default {
        name: "SettingsAccountEditNameAndLastName",
        data() {
            return {
                buttonIsDisabled: false,
                validForm: false,
                snackbar: false,
                snackbarText: null,
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD')
},
                closeText: null,
                changeNameAndLastNameText: null,
                nameText: null,
                lastNameText: null,
                saveText: null,
                name: this.$store.getters['user/getName'],
                lastName: this.$store.getters['user/getLastName']
}
        },
        methods: {
            ...mapActions('user', ['updateAccountNameAndLastName']),

            saveNameAndLastName: function () {
                let data = {};
                let infoToUpdate = false;

                if (this.name != this.$store.getters['user/getName']) {
                    data = {
                        'name': this.name,
                        ...data
                    }
                    infoToUpdate = true;
                }

                if (this.lastName != this.$store.getters['user/getLastName']) {
                    data = {
                        'lastName': this.lastName,
                        ...data
                    }
                    infoToUpdate = true;
                }

                if (infoToUpdate) {
                    let resultUpdateAccountNameAndLastName = this.updateAccountNameAndLastName(data);

                    resultUpdateAccountNameAndLastName.then(() => {
                        this.$router.go(-1);
                    })
                        .catch((error) => {
                            this.snackbarText = error;
                            this.snackbar = true;
                        });
                }
                else{
                    this.$router.go(-1);
                }

            }
        },
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.changeNameAndLastNameText = this.$i18n.t('messages.TEXT_CHANGE_NAME_AND_LASTNAME');
            this.nameText = this.$i18n.t('messages.TEXT_NAME');
            this.lastNameText = this.$i18n.t('messages.TEXT_LASTNAME');
            this.saveText = this.$i18n.t('messages.TEXT_SAVE');
        }
    }
</script>

<style scoped>

</style>